a {
  color: #248a83;
  text-decoration: none; }
  a:visited {
    color: #19625d; }
  a:hover, a:focus, a:active {
    color: #19625d; }
  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
  font-size: 16px;
  font-size: 1rem; }

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit; }

body {
  background: #fff;
  /* Fallback for when there is no custom background color defined. */ }

blockquote,
q {
  quotes: '' ''; }
  blockquote:before, blockquote:after,
  q:before,
  q:after {
    content: ''; }

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5rem; }

ul,
ol {
  margin: 0 0 1.5rem 3rem; }

ul {
  list-style: disc; }

ol {
  list-style: decimal; }

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5rem; }

dt {
  font-weight: bold; }

dd {
  margin: 0 1.5rem 1.5rem; }

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

table {
  margin: 0 0 1.5rem;
  width: 100%; }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Buttons */
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 1px solid;
  border-color: #ccc #ccc #bbb;
  border-radius: 3px;
  background: #e6e6e6;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.5), inset 0 15px 17px rgba(255, 255, 255, 0.5), inset 0 -5px 12px rgba(0, 0, 0, 0.05);
  color: rgba(0, 0, 0, 0.8);
  font-size: 12px;
  font-size: 0.75rem;
  line-height: 1;
  padding: .6rem 1rem .4rem;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.8); }
  button:hover,
  input[type="button"]:hover,
  input[type="reset"]:hover,
  input[type="submit"]:hover {
    border-color: #ccc #bbb #aaa;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.8), inset 0 15px 17px rgba(255, 255, 255, 0.8), inset 0 -5px 12px rgba(0, 0, 0, 0.02); }
  button:active, button:focus,
  input[type="button"]:active,
  input[type="button"]:focus,
  input[type="reset"]:active,
  input[type="reset"]:focus,
  input[type="submit"]:active,
  input[type="submit"]:focus {
    border-color: #aaa #bbb #bbb;
    box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.5), inset 0 2px 5px rgba(0, 0, 0, 0.15); }

/* Fields */
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
  color: #666;
  border: 1px solid #ccc;
  border-radius: 3px; }
  input[type="text"]:focus,
  input[type="email"]:focus,
  input[type="url"]:focus,
  input[type="password"]:focus,
  input[type="search"]:focus,
  textarea:focus {
    color: #111; }

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
  padding: 3px; }

textarea {
  padding-left: 3px;
  width: 100%; }

/*--------------------------------------------------------------
# Reusable utility classes 
--------------------------------------------------------------*/
.screen-reader-text {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important; }
  .screen-reader-text:focus, .screen-reader-text:active {
    clip: auto !important;
    -webkit-clip-path: none !important;
    clip-path: none !important;
    height: auto !important;
    margin: auto !important;
    overflow: visible !important;
    width: auto !important;
    white-space: normal !important; }

.container {
  max-width: 1120px;
  margin-right: auto;
  margin-left: auto; }

.flex-split {
  display: flex; }
  .flex-split > * {
    width: 50%; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/* Captions */
.wp-caption {
  margin-bottom: 1.5rem;
  max-width: 100%; }
  .wp-caption img[class*='wp-image-'] {
    display: block;
    margin-left: auto;
    margin-right: auto; }
  .wp-caption .wp-caption-text {
    margin: 0.8075rem 0; }

.wp-caption-text {
  text-align: center; }

/*--------------------------------------------------------------
# Site-wide styles 
--------------------------------------------------------------*/
.site {
  max-width: 1120px;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  justify-content: space-between;
  width: 100%; }
  .site > * {
    flex-grow: 1; }

.site--splash {
  max-width: unset;
  text-align: center; }
  .site--splash *:not(:first-child) {
    max-width: 1120px;
    margin-right: auto;
    margin-left: auto; }
  .site--splash .entry-title {
    color: #fff; }

.site__main {
  flex-grow: 1; }

/*--------------------------------------------------------------
# Site header styles 
--------------------------------------------------------------*/
.site-header {
  max-width: 1120px;
  margin-right: auto;
  margin-left: auto; }

.site-header--above {
  position: absolute;
  top: 0;
  left: 0;
  right: 0; }

.site-header__nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 1.5rem;
  z-index: 9999; }
  .site-header__nav ul {
    list-style: none;
    margin: 0;
    padding-left: 0;
    display: none; }
  .site-header__nav li {
    position: relative;
    display: inline-block; }
  .site-header__nav a {
    color: #fff; }
  .site-header__nav > * * {
    padding: 0 0.5rem;
    text-transform: uppercase; }

.site-header__nav--no-bg {
  border-bottom: 1px solid #e1e1e1; }
  .site-header__nav--no-bg a {
    color: #248a83; }

.site-header__logo {
  height: 2.1rem;
  min-width: 3.45rem; }

.site-header__title {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important; }

/* collapsed */
.site-header__nav.toggled ul {
  display: unset; }

@media screen and (min-width: 768px) {
  .site-header__nav--closed {
    display: none; }
  .site-header__nav ul {
    display: unset; } }

/*--------------------------------------------------------------
# Sidebar 
--------------------------------------------------------------*/
.sidebar {
  flex-grow: 0; }

/*--------------------------------------------------------------
# Post-related styling
--------------------------------------------------------------*/
.updated:not(.published) {
  display: none; }

/*--------------------------------------------------------------
# Blog-related styles 
--------------------------------------------------------------*/
.entry-header--float {
  position: relative;
  width: 100%; }

.entry-header__img--fill {
