/*--------------------------------------------------------------
# Shop 
--------------------------------------------------------------*/
.shop__grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr); }

/*--------------------------------------------------------------
# Button styles 
--------------------------------------------------------------*/
.button {
  padding: 0.75rem;
  font-size: 0.85rem;
  border: 1px solid #000;
  color: #000;
  text-transform: uppercase;
  text-decoration: none;
  padding-left: 1.125rem;
  padding-right: 1.125rem; }
  .button, .button:visited {
