/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/

.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0;
}

/* Make sure embeds and iframes fit their containers. */

embed,
iframe,
object {
  max-width: 100%;
}

/* Captions */

.wp-caption {
  margin-bottom: $base__space-unit;
  max-width: 100%;

  img[class*='wp-image-'] {
    @include center-block;
  }

  .wp-caption-text {
    margin: 0.8075rem 0;
  }
}

.wp-caption-text {
  text-align: center;
}
